.nav-container {
  height: 100%;
  width: 100%;
}

.nav {
  display: flex; 
  flex-direction: column; 
  justify-content: center;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%), #BB4B82;
  border-radius: 5px;
}

.title {
  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #FFFFFF;
  padding-top: 15px;
  padding-bottom: 15px;
}

.selection-container {
  display: flex;
  flex-direction: row; 
  justify-content: space-evenly;
  margin-bottom: 10px;
}

.selection0 {
  width: 30%;
  display: flex;
  flex-direction: column;
  text-align: center;
  opacity: 1;
}

.selection1 {
  width: 30%;
  display: flex;
  flex-direction: column;
  text-align: center;
  opacity: .4;
}

.selection2 {
  width: 30%;
  display: flex;
  flex-direction: column;
  text-align: center;
  opacity: .4;
}

.heading {
  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 9px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #FFFFFF;
  display: flex;
  justify-content: center;
}

.bar {
  border: 2px solid white;
  border-radius: 20px;
  color: #FFFFFF;
  margin-bottom: 5px;
}

.button-container {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.add-button {
  background: #FFFFFF;
  border: 1px solid #FFFFFF;
  border-radius: 5px;

  font-family: Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #009CDB;
  text-align: center;
  padding: 10px 15px;
}

.done-button {
  background: #FFFFFF;
  border: 1px solid #FFFFFF;
  border-radius: 5px;

  font-family: Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #333333;
  text-align: center;
  padding: 10px 15px;
}

