#edit-button {
  margin-top: 15px;
  margin-left: 10px;

  background: #FFFFFF;
  border: 1px solid #FFFFFF;
  border-radius: 5px;

  font-family: Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #009CDB;
  text-align: center;
  padding: 10px 15px;
}