.carousel-provider {
  margin-left: 15px;
  /* padding-bottom: 30px; */
  /* height: 150% */
}

.slider, .tray, .tray-wrap {
  padding-bottom: 10px;
}

.slide-container {
  display: flex; 
  flex-direction: column; 
  align-items: center; 
  
  border: 1px solid #E4E4E4;
  border-radius: 10px;

  margin-left: 5px;
  margin-right: 5px;
}

.image {
  height: 100px;
  margin: 5px 0px;
  width: auto;
}

.p-image {
  max-height: 100%;
  width: auto;
  margin: 5px 0px;
}

.name {
  margin-top: 15px;
  height: 40px;
  padding: 0px 15px;

  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  text-align: center;
  letter-spacing: 0.08em;
  color: #333333;
  overflow: hidden;

}

.price {
  font-family: Futura;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  text-align: center;
  letter-spacing: 0.04em;
  color: #333333;

  margin-bottom: 20px;
}

.add {
  display: flex;
  justify-content: center;
  align-content: center;
  /* position: 'absolute'; */
  position: fixed;
  bottom: -5px;

  display: none;
} 


.button {
  padding: 5px 20px;
  
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #009CDB;
  border: none;
  border-radius: 5px; 
  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  text-align: center;
  letter-spacing: 0.08em;
  color: #FFFFFF;
} 
